<template>
  <div>
    <adminNav />
    <div v-if="isLoading">
      <spinner />
    </div>
    <div v-else class="container mb-5">
      <div class="text-left">
        <h1></h1>
      </div>
      <div class="table-responsive-md">
        <table class="table table-striped">
          <thead class="thead-dark">
            <tr>
              <!-- <th scope="col">id</th> -->
              <!-- <th scope="col">ID</th> -->
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
              <th scope="col"></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="order in orders" :key="order.id">
              <!-- <td scope="row">
                {{order.id}}
              </td> -->
              <!-- <td scope="row">{{order.UserId}}</td> -->
              <td scope="row">{{order.receiver_name}}</td>
              <td scope="row">{{order.phone}}</td>
              <td scope="row"><router-link :to="{name:'adminOrder', params:{ order_id:order.id }}">{{order.sn || ""}}</router-link></td>
              <td scope="row">{{order.total_price}}</td>
              <td scope="row">{{order.payment_method}}</td>
              <td scope="row">{{order.payment_status==1?"":order.payment_status==99?"":""}}</td>
              <td scope="row">{{order.shipping_status==0?"":""}}</td>
              <td scope="row">
                <button
                  type="button"
                  class="btn btn-outline-success btn-sm"
                  id="updateBtn"
                  @click.prevent.stop="postTransition(order)"
                ></button>
              </td>
              <!-- Modal -->
              <div
                class="modal fade"
                id="orderModal"
                tabindex="-1"
                role="dialog"
                aria-labelledby="exampleModalScrollableTitle"
                aria-hidden="true"
              >
                <div class="modal-dialog modal-dialog-scrollable" role="document">
                  <div class="modal-content">
                    <div class="modal-header">
                      <h5 class="modal-title" id="exampleModalScrollableTitle">{{order.sn}}</h5>
                      <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                      </button>
                    </div>
                    <div class="modal-body text-left">
                      <p>{{spgResponse.MerchantID}}</p>
                      <p>{{spgResponse.Amt}}</p>
                      <p>{{spgResponse.TradeNo}}</p>
                      <p>
                        {{spgResponse.TradeStatus}}
                        <br />
                        <small class="text-muted">: 0= 1= 2= 3=</small>
                      </p>
                      <p>
                        {{spgResponse.PaymentType}}
                        <br />
                        <small
                          class="text-muted"
                        >: CREDIT= / CVSCOM= / VACC=ATM / WEBATM= / BARCODE= / CVS=</small>
                      </p>
                      <p>{{spgResponse.CreateTime}}</p>
                      <p>{{spgResponse.PayTime}}</p>
                      <p>CheckCode{{spgResponse.CheckCode}}</p>
                      <p>{{spgResponse.FundTime}}</p>
                      <hr />
                      <div v-if="spgResponse.PaymentType=='CREDIT'">
                        <p class="text-success"></p>
                        <p>
                          {{spgResponse.PaymentMethod}}
                          <br />
                          <small
                            class="text-muted"
                          >: CREDIT =  / FOREIGN =  / NTCB =  / UNIONPAY =  / APPLEPAY = ApplePay / GOOGLEPAY = GooglePay / SAMSUNGPAY = SamsungPay</small>
                        </p>
                        <p>{{spgResponse.RespondCode}}</p>
                        <p>{{spgResponse.Auth}}</p>
                        <p>{{spgResponse.CloseAmt}}</p>
                        <p>{{spgResponse.CloseStatus}}</p>
                        <p>{{spgResponse.BackBalance}}</p>
                        <p>{{spgResponse.BackStatus}}</p>
                        <p>{{spgResponse.RespondMsg}}</p>
                        <p>-{{spgResponse.Inst}}</p>
                        <p>-{{spgResponse.InstFirst}}</p>
                        <p>-{{spgResponse.InstEach}}</p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </tr>
          </tbody>
        </table>
      </div>
      <nav v-if="totalPage > 1" aria-label="Page navigation">
        <ul class="pagination">
          <li v-show="previousPage" class="page-item">
            <router-link
              class="page-link"
              aria-label="Previous"
              :to="{name: 'adminOrders', query: { page: previousPage }}"
            >
              <span aria-hidden="true">&laquo;</span>
            </router-link>
          </li>
          <li
            v-for="page in totalPage"
            :key="page"
            :class="['page-item', { active: currentPage === page }]"
          >
            <router-link class="page-link" :to="{name: 'adminOrders', query: { page }}">{{ page }}</router-link>
          </li>
          <li v-show="nextPage" class="page-item">
            <router-link
              class="page-link"
              :to="{name: 'adminOrders', query: { page: nextPage }}"
              aria-label="Next"
            >
              <span aria-hidden="true">&raquo;</span>
            </router-link>
          </li>
        </ul>
      </nav>
    </div>
  </div>
</template>
<script>
import spinner from "./../components/spinner";
import adminNav from "./../components/adminNav";
import adminAPI from "./../apis/admin";
import { Toast } from "./../utils/helpers";
import ordersAPI from "./../apis/orders";
import $ from "jquery";
export default {
  components: {
    adminNav,
    spinner
  },
  data() {
    return {
      orders: [],
      spgResponse: [],
      totalPage: 0,
      currentPage: 0
    };
  },
  created() {
    const { page } = this.$route.query;
    this.fetchAdminOrders({ page });
  },
  beforeRouteUpdate(to, from, next) {
    const { page } = to.query;
    this.fetchAdminOrders({ page });
    next();
  },
  computed: {
    previousPage() {
      return this.currentPage === 1 ? null : this.currentPage - 1;
    },
    nextPage() {
      return this.currentPage + 1 > this.totalPage
        ? null
        : this.currentPage + 1;
    },
    isLoading() {
      return this.$store.state.isLoading;
    }
  },
  methods: {
    async fetchAdminOrders({ page = 1 }) {
      try {
        this.$store.dispatch("updateLoading", true);
        const { data, statusText } = await adminAPI.orders.get({
          page
        });
        console.log(data)
        if (statusText !== "OK") {
          throw new Error(statusText);
        }

        this.orders = data.orders;
        this.currentPage = data.page;
        this.totalPage = data.totalPage.length;
        this.$store.dispatch("updateLoading", false);
      } catch (error) {
        this.$store.dispatch("updateLoading", false);
        Toast.fire({
          icon: "error",
          title: ""
        });
      }
    },
    async postTransition(order) {
      let amt = order.total_price;
      let sn = order.sn;
      try {
        const { data, statusText } = await ordersAPI.postTransition({
          amt,
          sn
        });

        console.log(data)
        if (statusText !== "OK") {
          throw new Error(statusText);
        }
        this.spgResponse = data.response.Result;
        await this.fetchAdminOrders(1);
        this.openOrderModal();
      } catch (error) {
        // Toast.fire({
        //   icon: "error",
        //   title: "/"
        // });
        console.log(error)
      }
    },
    openOrderModal() {
      $("#orderModal").modal("show");
    }
  }
};
</script>